  width: 20px;
  height: 2px;
  background-color: #000;
  margin: 5px 0;
  /*filter: blur(1px);*/
}
.bottom-btn[_v-6b595a70] {
  position: fixed;
  z-index: 10;
  bottom: 120px;
  left: 50%;
  cursor: pointer;
}
/*动画*/
.btn-normal[_v-6b595a70] {
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-animation-name: btn-transition;
          animation-name: btn-transition;
  -webkit-animation: btn-normal-transition .5s;
          animation: btn-normal-transition .5s;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
@-webkit-keyframes btn-normal-transition {
  0% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes btn-normal-transition {
  0% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.btn-upper-arrow[_v-6b595a70] {
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-animation-name: btn-transition;
          animation-name: btn-transition;
  -webkit-animation: btn-upper-arrow-transition .5s;
          animation: btn-upper-arrow-transition .5s;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
@-webkit-keyframes btn-upper-arrow-transition {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
}
@keyframes btn-upper-arrow-transition {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
}
